import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';
import Demo6 from './demo6';
import Demo7 from './demo7';

class Page extends PureComponent {
    displayName = 'Page';
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Input </h1>
                <blockquote className='page-tip'>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Row>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo1></Demo1>
                        </Label>
                        <Label className='code-box'>
                            <Demo3></Demo3>
                        </Label>
                        <Label className='code-box'>
                            <Demo5/>
                        </Label>
                        <Label className='code-box'>
                            <Demo7/>
                        </Label>
                    </Col>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo2></Demo2>
                        </Label>
                        <Label className='code-box'>
                            <Demo4/>
                        </Label>
                        <Label className='code-box'>
                            <Demo6/>
                        </Label>
                    </Col>
                </Row>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Input</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'value', desc: '', type: 'string', default: ''},
                    {param: 'type', desc: 'input integernumber', type: 'string', default: 'text'},
                    {param: 'trigger', desc: 'change', type: 'string', default: 'blur'},
                    {param: 'onChange', desc: ' trigger', type: 'function', default: ''},
                    {param: 'grid', desc: 'Inputgrid={1/4}', type: 'number/object', default: ''},
                    {param: 'size', desc: 'Inputlargesmall', type: 'string', default: ''},
                    {param: 'addonBefore', desc: '', type: 'any', default: ''},
                    {param: 'addonAfter', desc: '', type: 'any', default: ''},
                    {param: 'prefix', desc: '', type: 'any', default: ''},
                    {param: 'suffix', desc: '', type: 'any', default: ''}
                ]}></Table>
                <div className='mt-5'>htmlinput</div>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>getValue()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {'String'} Input
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setValue(value)</i>
                        <span></span>
                        <ul>
                            <li>
                                value {'String'} Input
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



